import { convertColorToRGBA } from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataMassageUtils';
import { THEME_TOKEN_NAMES } from '../../const/themeTokensConst';
import { CORE_COLOR_NEUTRAL_DEFAULTS } from '../../const/colorNeutralTokenDefaultsConst';

const {
    TOASTER_PROGRESS_BAR_COLOR,
    TOASTER_ERROR_COLOR_ICON,
    TOASTER_INFO_COLOR_ICON,
    TOASTER_SUCCESS_COLOR_ICON,
    TOASTER_WARNING_COLOR_ICON,
    CORE_COLOR_NEUTRAL_BLACK,
    CORE_COLOR_NEUTRAL_WHITE,
} = THEME_TOKEN_NAMES;

// TODO: Need accessibility evaluation for all variant icon colors

export const getToasterAffirmativeTheme = (sourceColorData = {}) => {
    const { base = {} } = sourceColorData;

    return {
        [getThemeAPIKeyFromName(TOASTER_SUCCESS_COLOR_ICON)]: base.hex,
    };
};

export const getToasterCautionaryTheme = (sourceColorData = {}) => {
    const { base = {} } = sourceColorData;

    return {
        [getThemeAPIKeyFromName(TOASTER_WARNING_COLOR_ICON)]: base.hex,
    };
};

export const getToasterErrorTheme = (sourceColorData = {}) => {
    const { base = {} } = sourceColorData;

    return {
        [getThemeAPIKeyFromName(TOASTER_ERROR_COLOR_ICON)]: base.hex,
    };
};

export const getToasterInfoTheme = (sourceColorData = {}) => {
    const { base = {} } = sourceColorData;

    return {
        [getThemeAPIKeyFromName(TOASTER_INFO_COLOR_ICON)]: base.hex,
    };
};

export const getToasterTheme = (config = {}) => {
    const { isDarkMode } = config;

    return {
        [getThemeAPIKeyFromName(TOASTER_PROGRESS_BAR_COLOR)]:
            convertColorToRGBA(
                isDarkMode
                    ? CORE_COLOR_NEUTRAL_DEFAULTS[CORE_COLOR_NEUTRAL_WHITE]
                    : CORE_COLOR_NEUTRAL_DEFAULTS[CORE_COLOR_NEUTRAL_BLACK],
                0.3,
                true,
            ),
    };
};
