    const { ICON, TEXT, BG } = getCalloutMemoColors(sourceColorData);

    return {
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_ICON)]: ICON,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_TEXT)]: TEXT,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_ERROR_COLOR_BG)]: BG,
    };
