export const getCalloutTheme = (colors = {}, config = {}) => {
    const {
        errorColorData = {},
        canvasColor = {},
        canvasAltColor,
        textAltColor,
    } = colors;
    const { isDarkMode, shouldAutoCorrectColors = true } = config;
    const bgLightness = isDarkMode ? 20 : 80;
    const calloutMemoBgColor = getNewColorByChangingLightness(
        canvasColor.hex,
        bgLightness,
    );
    const calloutMemoTextColor = getTextColor(calloutMemoBgColor);

    const genericErrorCalloutIconColor = !shouldAutoCorrectColors
        ? errorColorData?.base?.hex
        : getJuxtaposedColorAgainstCanvases({
              canvasColor,
              canvasAltColor,
              sourceColorData: errorColorData,
          }).hex;
    const themeData = {
        [getThemeAPIKeyFromName(CALLOUT_COLOR_ICON)]: textAltColor.hex,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_BG)]: calloutMemoBgColor,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_ICON)]: calloutMemoTextColor,
        [getThemeAPIKeyFromName(CALLOUT_MEMO_COLOR_TEXT)]: calloutMemoTextColor,
        [getThemeAPIKeyFromName(CALLOUT_GENERIC_ERROR_COLOR_ICON)]:
            genericErrorCalloutIconColor,
    };

    return themeData;
};
