/* eslint-disable import/prefer-default-export */
import { getNewColorByChangingLightness } from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getTextColor } from '../colorUtils';
import { getThemeAPIKeyFromName } from '../dataMassageUtils';
import { THEME_TOKEN_NAMES } from '../../const/themeTokensConst';

const { AVATAR_COLOR_BG, AVATAR_COLOR_TEXT } = THEME_TOKEN_NAMES;

export const getAvatarTheme = (canvasColor = {}, config = {}) => {
    const { isDarkMode } = config;
    const bgLightness = isDarkMode ? 20 : 80;
    const bgColor = getNewColorByChangingLightness(
        canvasColor.hex,
        bgLightness,
    );
    const textColor = getTextColor(bgColor);

    return {
        [getThemeAPIKeyFromName(AVATAR_COLOR_BG)]: bgColor,
        [getThemeAPIKeyFromName(AVATAR_COLOR_TEXT)]: textColor,
    };
};
