/* eslint-disable import/prefer-default-export */
import { getTextColor } from '../colorUtils';
import { getThemeAPIKeyFromName } from '../dataMassageUtils';
import { THEME_TOKEN_NAMES } from '../../const/themeTokensConst';

const {
    ACCORDION_COLOR_BORDER,
    ACCORDION_COLOR_BG,
    ACCORDION_TRIGGER_COLOR_TEXT,
    ACCORDION_TRIGGER_HOVER_COLOR_BG,
    ACCORDION_TRIGGER_HOVER_COLOR_TEXT,
} = THEME_TOKEN_NAMES;

export const getAccordionTheme = (colors = {}) => {
    const {
        canvasColor = {},
        universalHoverColor = {},
        universalBorderColor = {},
    } = colors;

    return {
        [getThemeAPIKeyFromName(ACCORDION_COLOR_BORDER)]:
            universalBorderColor.hex,
        [getThemeAPIKeyFromName(ACCORDION_COLOR_BG)]: canvasColor.hex,
        [getThemeAPIKeyFromName(ACCORDION_TRIGGER_COLOR_TEXT)]: getTextColor(
            canvasColor.hex,
        ),
        [getThemeAPIKeyFromName(ACCORDION_TRIGGER_HOVER_COLOR_BG)]:
            universalHoverColor.hex,
        [getThemeAPIKeyFromName(ACCORDION_TRIGGER_HOVER_COLOR_TEXT)]:
            getTextColor(universalHoverColor.hex),
    };
};
