/* eslint-disable import/prefer-default-export */
import { getColorCorrelationsData } from '@Green-Dot-Corporation/eureka-lib-color-utils';
import { getThemeAPIKeyFromName } from '../dataMassageUtils';
import {
    getCommonColorCompatabilities,
    getCommonIds,
    getFocusHaloRGBA,
} from '../colorUtils';
import { THEME_TOKEN_NAMES } from '../../const/themeTokensConst';

const { LINK_COLOR_TEXT, LINK_FOCUS_COLOR_HALO, LINK_TEXT_DECORATION } =
    THEME_TOKEN_NAMES;

const getPossibleLinkColorsComparedToCanvases = (
    sourceColorData = {},
    canvasColor = {},
    canvasAltColor = {},
) => {
    const { base = {}, variants = [] } = sourceColorData;
    const canvasOptions = [base, ...variants];
    const evaluatedCanvasColor = getColorCorrelationsData(
        canvasColor,
        canvasOptions,
    );
    const evaluatedCanvasAltColor = getColorCorrelationsData(
        canvasAltColor,
        canvasOptions,
    );
    const { compatibleTextColors } = getCommonColorCompatabilities(
        evaluatedCanvasColor,
        evaluatedCanvasAltColor,
    );

    return compatibleTextColors;
};

const getPossibleColorsComparedToText = (
    sourceColorData = {},
    textColor = {},
) => {
    const { base = {}, variants = [] } = sourceColorData;
    const textOptions = [base, ...variants];
    const evaluatedTextColor = getColorCorrelationsData(textColor, textOptions);
    const { compatibleNonTextColors } = evaluatedTextColor;

    return compatibleNonTextColors;
};

const getLinkColor = (colors = {}, shouldAutoCorrectColors = true) => {
    const {
        sourceColorData = {},
        canvasColor = {},
        canvasAltColor = {},
        textColor = {},
    } = colors;
    const { base = {}, variants = [] } = sourceColorData;

    if (!shouldAutoCorrectColors) {
        return {
            textColor: base.hex,
            shouldUnderlineLink: false,
        };
    }

    const { isDark } = base;
    // Use `min` to get the darkest variant, `max` to get the lightest
    const mathMethod = isDark ? Math.min : Math.max;

    // Get the IDs of accessible colors compared to
    // both canvas colors
    const possibleLinkColorIdsComparedToCanvases =
        getPossibleLinkColorsComparedToCanvases(
            sourceColorData,
            canvasColor,
            canvasAltColor,
        );

    // Get the IDs of accessible colors compared to
    // the base text color
    const possibleLinkColorIdsComparedToText = getPossibleColorsComparedToText(
        sourceColorData,
        textColor,
    );

    // Get the IDs of accessible color options that work
    // with both canvas colors and surrounding text color
    const commonIds = getCommonIds(
        possibleLinkColorIdsComparedToCanvases,
        possibleLinkColorIdsComparedToText,
    );

    // If an accessible color cannot be found,
    // we should underline the link...
    const shouldUnderlineLink = commonIds.length === 0;
    // ...and fall back to ensuring the text is accessible
    // with botht he canvas colors
    const options = !shouldUnderlineLink
        ? commonIds
        : possibleLinkColorIdsComparedToCanvases;

    // Always check to see if we can use
    // the base color provided...
    const shouldUseSource = options.indexOf('base') > -1;

    // If there are no accessible options to choose from after all checks
    // or we know we can use the source color...
    if (options.length === 0 || shouldUseSource) {
        return {
            textColor: base.hex,
            shouldUnderlineLink,
        };
    }

    const variantId = mathMethod(...options).toString();

    return {
        textColor: variants.find((variant) => variant.id === variantId)?.hex,
        shouldUnderlineLink,
    };
};

export const getLinkTheme = (colors = {}, config = {}) => {
    const { shouldAutoCorrectColors } = config;
    const { textColor, shouldUnderlineLink } = getLinkColor(
        colors,
        shouldAutoCorrectColors,
    );

    return {
        [getThemeAPIKeyFromName(LINK_COLOR_TEXT)]: textColor,
        [getThemeAPIKeyFromName(LINK_TEXT_DECORATION)]: shouldUnderlineLink
            ? 'underline'
            : 'none',
        [getThemeAPIKeyFromName(LINK_FOCUS_COLOR_HALO)]:
            getFocusHaloRGBA(textColor),
    };
};
